.found_content{
      width: 100%;
      height: 100vh;
      min-height: 92rem;
      position: fixed;
      background: var(--bg-200);
      z-index: 10;
}

.found_container{
      flex-direction: column;
      align-items: center;
}

.found_logo h1{
      font-size: 8rem;
      text-transform: uppercase;
      font-weight: 900;
      color: var(--dark);
}

.found_logo h1 span{
      font-size: 8rem;
      color: var(--theme-200);
}

.found_img{
      width: 18.2rem;
}

.found_container h2{
      font-size: 2.4rem;
      margin-top: 2rem;
}

.found_container p{
      max-width: 50rem;
      text-align: center;
      margin: 2rem 0;
}

.found_container a{
      width: 20rem;
      height: 5rem;
      border-radius: .4rem;
      border: solid .2rem var(--theme-300);
      box-shadow: 1rem 1rem var(--theme-300);
      position: relative;
      background: var(--theme-200);
      overflow: hidden;
}

.found_container a:before{
      width: 2rem;
      height: 8rem;
      background: var(--light);
      top: calc(50% - 5rem);
      transform: rotate(35deg);
      left: -5rem;
      box-shadow: 1rem 0 var(--theme-200), 1.5rem 0 var(--light);
}

.found_container a:hover:before{
      animation: move_btn .6s ease-in-out;
}

@keyframes move_btn{
      100%{
            left: 120%;
      }
}

.found_container a span{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      color: var(--text-100);
      font-weight: 500;
      font-size: 1.4rem;
}