.inner_home{
      width: 100%;
      height: 100%;
}

.home_header{
      width: 100%;
      height: 15.5rem;
      position: relative;
      align-items: center;
      justify-content: flex-end;
}

.home_header__avatar{
      position: absolute;
      width: 15.5rem;
      height: 15.5rem;
      border: solid 1rem var(--theme-200);
      border-radius: 50%;
      overflow: hidden;
      top: 0;
      left: 2rem;
      z-index: 2;
      flex-shrink: 1;
      box-shadow: 0 0 0 1.5rem rgba(0,0,0,.3);
}

.home_header__banner{
      width: 32rem;
      height: 15rem;
      background: var(--theme-200);
      position: relative;
      box-shadow:1rem 1rem 10px rgba(0,0,0,.2), 1rem -1rem var(--theme-200);
}

.home_header__banner_img{
      width: 100%;
      height: 100%;
      background: inherit;
      position: absolute;
      top: 0;
      left: 0;
}

.home_header__banner_img img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.home_header__banner_data{
      width: 100%;
      height: 100%;
      position: relative;
      padding: 1.5rem 4rem;
      flex-direction: column;
      justify-content: flex-end;
      color: var(--text-100);
}

.home_location{
      align-items: first baseline;
      gap: .5rem;
}

.home_location *{
      font-size: 2.5rem;
}

.home_location strong{
      text-transform: uppercase;
      font-size: 1.6rem;
      font-weight: 500;
}

.home_header__banner_data h2{
      font-size: 4rem;
      font-family: var(--ft-t-2);
}

.home_title{
      width: 100%;
      height: 9.5rem;
      position: relative;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 4rem;
}

.home_title:before,
.home_title:after{
      width: 4rem;
      height: 100%;
      background: var(--theme-200);
      top: 0;
}

.home_title:before{
      left: -3rem;
}

.home_title:after{
      right: -3rem;
}

.home_title{
      text-transform: uppercase;
      letter-spacing: .1rem;
}

.home_title h1{
      text-transform: uppercase;
      font-family: var(--ft-t-1);
      font-size: 6.5rem;
      line-height: 5.5rem;
      font-weight: 300;
}

.home_description{
      width: 44.2rem;
      height: 13rem;
      margin-top: 3rem;
      margin-left: calc(50% - 22.1rem);
      background: var(--theme-100);
      padding: 2rem;
      position: relative;
      border-radius: 2rem;
}

.home_description:before{
      width: 2rem;
      height: 2rem;
      background: inherit;
      left: calc(50% - 1rem);
      bottom: -1rem;
      transform: rotate(45deg);
}

.home_description:after{
      width: 100%;
      height: 0;
      border-bottom: dashed .2rem var(--theme-100);
      left: 0;
      bottom: -2.5rem;
}

.icon_quote{
      width: 4rem;
      height: 4rem;
      position: absolute;
}

.icon_quote *{
      font-size: 5rem;
}
.icon_quote.left{
      top: 0;
      left: 0;
      transform: translate(-40%, -40%);
}

.icon_quote.right{
      bottom: 0;
      right: 0;
      transform: translate(40%, 40%);
}

.home_description p{
      font-size: 1.4rem;
      font-family: var(--ft-t-1);
      white-space: pre-line;
}