.user_link{
      width: 5rem;
      height: 5rem;
      background: var(--theme-200);
      border-radius: 50%;
      position: relative;
}

.user_link:before{
      content: attr(data-name);
      width: 6rem;
      height: 2.6rem;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 50%;
      left: 110%;
      background: var(--bg-400);
      color: var(--text-100);
      text-transform: uppercase;
      font-size: 1rem;
      border-radius: .3rem;
      transform: translateY(-50%);
      opacity: 0;
      visibility: hidden;
      transition: .2s;
}

.user_link:after{
      width: 1.4rem;
      height: 1.4rem;
      top: 50%;
      right: -1.5rem;
      background: var(--bg-400);
      border-radius: .3rem;
      transform: translateY(-50%) rotate(45deg);
      opacity: 0;
      visibility: hidden;
      transition: .2s;
}

.user_link:hover:before,
.user_link:hover:after{
      opacity: 1;
      visibility: visible;
}

.user_link *{
      color: var(--text-100);
      font-size: 3rem;
}