.online_content{
      gap: 1rem;
      align-items: center;
}

.online_avatar{
      width: 3.2rem;
      height: 3.2rem;
      background: var(--bg-400);
      position: relative;
      box-shadow: 0 0 0 .4rem var(--bg-400);
      border-radius: 50%;
}

.online_avatar__img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      overflow: hidden;
}

.online_dot{
      position: absolute;
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      bottom: 0;
      right: -.5rem;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}

.online_content span{
      color: var(--text-100);
      text-transform: uppercase;
      font-family: var(--ft-t-3);
      font-size: 1.2rem;
}