.aside_friends{
      width: 100%;
      height: auto;
      padding: 2.5rem;
      padding-top: 5rem;
      background: var(--dark);
      border-radius: 2rem;
      margin-top: 6rem;
      position: relative;
}

.aside_friends_search{
      width: 22rem;
      height: auto;
      background: var(--dark);
      padding: 1rem;
      position: absolute;
      top: -3rem;
      left: 2rem;
      border-radius: 2rem;
}

.input_group{
      width: 100%;
      height: 4.4rem;
      background: var(--theme-100);
      border-radius: 1rem;
      position: relative;
}

.input_group input{
      width: 100%;
      height: 100%;
      padding: 1rem;
      outline: none;
      border: none;
      background: none;
      color: var(--text-100);
}

.icon_search{
      position: absolute;
      width: 2.5rem;
      height: 4.4rem;
      top: 0;
      right: 1rem;
}

.icon_search *{
      color: var(--text-100);
      font-size: 2rem;
}

.aside_friends__list{
      width: 100%;
      height: auto;
}

.friend_row{
      width: 100%;
      height: 7.6rem;
      background: var(--theme-100);
      border-radius: 1rem;
      margin-top: .5rem;
      padding: 1rem;
      position: relative;
      gap: 1rem;
      align-items: center;
}

.friend_row:before,
.friend_row:after{
      height: 1rem;
      right: 1rem;
}

.friend_row:before{
      width: 3.2rem;
      background: var(--theme-200);
      top: 4rem;
      transform: translateX(2rem);
}

.friend_row:after{
      width: 2rem;
      background: var(--bg-400);
      top: 5.5rem;
      transform: translateX(2rem);
}

.friend_row__avatar{
      width: 5.6rem;
      height: 5.6rem;
      background: var(--dark);
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 0 .5rem rgba(0,0,0,.1);
}

.friend_row__info{
      flex: 1;
      height: 100%;
      position: relative;
}

.friend_row__info span{
      text-transform: uppercase;
      font-size: 1.4rem;
      margin-top: 1rem;
}

.friend_row__info p{
      color: var(--text-100);
      text-transform: capitalize;
      font-size: 1.2rem;
}

.friend_row__info a{
      position: absolute;
      width: 3.4rem;
      height: 3.4rem;
      color: var(--text-400);
      top: 0;
      right: 0;
      transition: .2s;
      z-index: 3;
}

.friend_row__info a *{
      font-size: 2rem;
}

.friend_row__info a:hover{
      color: var(--text-100);
}