@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Quicksand:wght@300..700&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');

:root{
    /** fonts */
    --ft-s-1: "Quicksand", sans-serif;
    --ft-t-2: "Lato", sans-serif;
    --ft-t-3: "Roboto Condensed", sans-serif;


    /** theme */
    --theme-100: #c9b590;
    --theme-200: #bd161d;
    --theme-300: #900f13;
    --theme-alpha: rgba(144,15,19,.85);
    --v-gradient: linear-gradient(90deg, var(--theme-2), var(--theme-1));
    --h-gradient: linear-gradient(-135deg, var(--theme-2), var(--theme-1));
    --a-gradient: linear-gradient(45deg, transparent 35%, var(--light) 100%);
    
    /** colors */
    --bg-400: #010101;
    --bg-300: #3a3a3a;
    --bg-200: #FDF7F7;
    --bg-100: #fff;
    --text-200: #010101;
    --text-100: #fff;
    --border: #d6d6d6;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;
    --dark: #212121;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #171717;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-t-3);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-400);
    color: var(--text-200);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-t-3);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    widtH: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.center{
    justify-content: center;
    align-items: center;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 98rem;
    height: auto;
    position: relative;
    margin: 0 auto;
}

@property --a{
    syntax: '<angle>';
    inherits: false;
    initial-value: 0deg;
}

.effect{
    position: relative;
    width: fit-content;
    height: fit-content;
    overflow: hidden;
}

.effect:before{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: repeating-conic-gradient(from var(--a), var(--primary) 0%, var(--primary) 5%, transparent 5%, transparent 40%, var(--primary) 50%);
    animation: rotating 4s linear infinite;
}

.title-g{
   width: fit-content;
   height: fit-content;
   color: transparent;
   background: var(--v-gradient);
   -webkit-background-clip: text;
   -moz-background-clip: text;
   background-clip: text;
}

.effect:after{
    background: var(--bg-200);
    top: .2rem;
    left: .2rem;
}

@keyframes rotating{
    0%{
        --a: 0deg;
    }
    100%{
        --a: 360deg;
    }
}

.scroll-y{
    width: 100%;
    height: 42.5rem;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-100), var(--theme-100);
}

.scroll-x{
    width: calc(100% - 13.6rem);
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-100), var(--theme-100);
}

.scroll-y::-webkit-scrollbar,
.scroll-x::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-100);
    border-radius: .8rem;
}

.scroll-y::-webkit-scrollbar-thumb,
.scroll-x::-webkit-scrollbar-thumb{
    background: var(--theme-200);
    border-radius: .8rem;
}

::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-100);
    border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
    background: var(--theme-200);
    border-radius: .8rem;
}

::placeholder{
    font-size: 1.4rem;
    color: var(--text-100);
    font-family: var(--ft-s-3);
    letter-spacing: .1rem;
}