.header_content{
      width: 100%;
      height: 33.5rem;
      position: relative;
      background: var(--theme-200);
      border-bottom: solid 1.5rem var(--theme-200);
      overflow: hidden;
}

.header_content:before,
.header_content:after{
      --width: calc(100vw - 65rem);
      width: var(--width);
      height: var(--width);
      background: var(--light);
      top: 50%;
      border-radius: 50%;
      z-index: 2;
}

.header_content:before{
      left: 0;
      transform: translate(-50%, -50%);
}

.header_content:after{
      right: 0;
      transform: translateY(-50%) translateX(50%);
}

.header_banner{
      width: 100%;
      height: 100%;
      background: inherit;
      position: absolute;
      top: 0;
      left: 0;
}

.header_banner img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.container.header{
      position: relative;
      height: 100%;
      z-index: 3;
}

.header_center{
      width: 73rem;
      height: 22.6rem;
      position: relative;
      margin-top: 4.7rem;
      margin-left: calc(50% - 36.5rem);
      flex-direction: column;
      align-items: center;
}

.header_center:before{
      width: 100%;
      height: 100%;
      border-radius: 4rem;
      background: var(--bg-400);
      left: 0;
      top: 0;
      -webkit-mask-image: radial-gradient(circle at -3.5rem center, transparent 0, transparent 7rem, var(--bg-400) 7.2rem, var(--bg-400));
      mask-image: radial-gradient(circle at -3.5rem center, transparent 0, transparent 7rem, var(--bg-400) 7.2rem, var(--bg-400));
}

.header_center__user{
      width: 100%;
      height: 21.2rem;
      margin-top: -2rem;
      position: relative;
      justify-content: center;
}

.user_aside__menu{
      width: 7.2rem;
      height: 100%;
      background: var(--bg-100);
      border-radius: 999px;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      padding: 1.1rem;
      flex-shrink: 0;
      position: absolute;
      top: 0;
      left: 8.5rem;
      z-index: 5;
}

.user_aside__menu:before{
      content: attr(data-title);
      white-space: no-wrap;
      width: 11rem;
      height: 3rem;
      background: var(--theme-200);
      text-align: center;
      line-height: 3rem;
      color: var(--text-100);
      border-radius: .2rem .2rem 0 0;
      top: 71%;
      left: -7rem;
      transform: rotate(-90deg) translateX(50%);
}

.user_avatar{
      width: 38rem;
      height: 21.2rem;
      flex-shrink: 0;
      background: var(--theme-200);
      border-radius: 4rem;
      position: absolute;
      top: 0;
      left: calc(50% - 19rem);
      overflow: hidden;
}

.user_header__data{
      width: 21.6rem;
      height: 15.2rem;
      padding: 2rem;
      color: var(--text-100);
      position: absolute;
      top: 4.5rem;
      right: 1.5rem;
      background: var(--theme-alpha);
      flex-direction: column;
      align-items: center;
}

.user_header__title{
      width: 100%;
      height: 7rem;
      position: relative;
      flex-direction: column;
      align-items: center;
}

.user_header__title h1{
      text-transform: uppercase;
      font-size: 3.25rem;
      position: relative;
      letter-spacing: -.2rem;
      font-weight: 500;
}

.user_header__title p{
      width: 100%;
      height: 4.7rem;
      overflow: hidden;
      position: absolute;
      bottom: .2rem;
      left: 0;
      font-size: .8rem;
      text-align: justify;
      opacity: .5;
}

.user_social__nav{
      margin-top: 1rem;
}

.header_center__navbar{
      width: 43.4rem;
      height: 4rem;
      border-radius: 999px;
      background: var(--theme-200);
      border: solid .3rem var(--bg-100);
      position: absolute;
      bottom: 0;
      left: calc(50% - 21.7rem);
      transform: translateY(1.5rem);
}

.header_center__navbar ul{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: .1rem;
      overflow: hidden;
}

.btn_navbar{
      width: 11.8rem;
      height: 3.4rem;
      position: relative;
}

.btn_navbar span{
      width: 100%;
      height: 3.4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--theme-200);
      color: var(--text-100);
      text-transform: uppercase;
      box-shadow: 0 3.4rem var(--bg-100);
      text-shadow: 0 3.4rem var(--theme-200);
      font-size: 1.2rem;
      margin-top: 0;
      transition: .3s ease-in-out;
}

.btn_navbar:hover span,
.btn_navbar.active span{
      margin-top: -3.4rem;
}