.empty_content{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      flex-direction: column;
}

.empty_content__img{
      width: 14rem;
}

.empty_content h1{
      font-size: 5rem;
      opacity: .2;
}

.empty_content p{
      font-size: 2rem;
      font-weight: 700;
      text-align: center;
      opacity: .4;
      margin-top: 2rem;
}

.empty_content p span{
      color: var(--green);
      font-size: 2rem;
      font-weight: 900;
      opacity: 1;
}