.main_content{
      margin-top: 1.5rem;
}

.container.main{
      gap: 3rem;
}

.aside_content{
      width: 26rem;
      height: auto;
      flex-shrink: 0;
}

.section_content{
      flex: 1;
      width: calc(100% - 29rem);
      height: auto;
}

.pages_content,
.pages_wrapper{
      width: 100%;
      height: 54.2rem;
      position: relative;
}

.pager_inner{
      width: 100%;
      height: 100%;
      background: var(--dark);
      border-radius: 2rem;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 1;
      visibility: visible;
      transition: .5s ease-in-out;
}

.pager_inner.move_left{
      opacity: 1;
      visibility: visible;
      transform: translateX(-100%);
      z-index: 10;
}

.pager_inner.move_right{
      opacity: 0;
      visibility: hidden;
      transform: translateX(100%);
}

@keyframes remove_page{
      0%{
            opacity: 1;
            visibility: visible;
            transform: translateX(0);
      }
      100%{
            opacity: 0;
            visibility: hidden;
            transform: translateX(120%);
      }
}

.pager_inner__content{
      width: 54rem;
      height: 100%;
      background: var(--bg-200);
      border-radius: 2rem;
      padding: 3rem;
      flex-shrink: 1;
}

.pager_inner__aside{
      width: 14.5rem;
      height: 100%;
}