.inner_relation{
      width: 100%;
      height: 100%;
      position: relative;
}

.inner_relation:before,
.inner_relation:after{
      width: 4rem;
      height: 9.5rem;
      background: var(--theme-200);
      top: calc(55% - 4.75rem);
}

.inner_relation:before{
      left: -3rem;
}

.inner_relation:after{
      right: -3rem;
}

.relation_header{
      width: 100%;
      height: 18rem;
      gap: 3rem;
      align-items: center;
      position: relative;
      padding-left: 4rem;
}

.relation_header__avatar{
      width: 18rem;
      height: 18rem;
      background: var(--theme-100);
      border-radius: 2rem;
      position: relative;
      flex-shrink: 0;
}

.relation_header__avatar:before,
.relation_header__avatar:after{
      width: 100%;
      height: 100%;
      border-radius: 2rem;
      top: 0;
      left: 0;
}

.relation_header__avatar:before{
      background: inherit;
      transform: rotate(15deg);
}

.relation_header__avatar:after{
      border: solid .3rem var(--theme-100);
      transform: rotate(-15deg);
}

.relation_header__avatar_img{
      width: 100%;
      height: 100%;
      border-radius: 2rem;
      position: relative;
      overflow: hidden;
}

.relation_header__data{
      flex-direction: column;
      height: 100%;
      justify-content: flex-end;
}

.relation_header__data span{
      text-transform: uppercase;
      font-size: 1.2rem;
      padding-left: .2rem;
}

.relation_header__data h2{
      font-size: 3.5rem;
      text-transform: uppercase;
      font-family: var(--ft-t-1);
      font-weight: 600;
      line-height: 3rem;
}

.relation_header__data h3{
      width: fit-content;
      position: relative;
      font-size: 3rem;
      font-family: var(--ft-t-2);
      color: var(--theme-100);
}

.relation_header__data h3:after{
      width: 3.5rem;
      height: .5rem;
      background: var(--theme-200);
      bottom: .5rem;
      right: 0;
      transform: translateX(100%);
}

.relation_header__icon{
      width: 5rem;
      height: 5rem;
      position: absolute;
      top: 0;
      right: 0;
      color: var(--theme-200);
      transform: scale(.5);
      animation: pulse .5s ease-in-out infinite;
}

.relation_header__icon *{
      font-size: 4rem;
}

@keyframes pulse{
      0%, 100%{
            transform: scale(.5);
      }
      50%{
            transform: scale(1);
      }
}

.relation_description{
      width: 42.4rem;
      height: auto;
      margin-top: 3.7rem;
      margin-left: calc(50% - 21.2rem);
      gap: 3rem;
}

.relation_description__image{
      width: 4.5rem;
      height: 15.5rem;
      flex-shrink: 0;
      background: var(--theme-200);
}

.relation_description__image img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.relation_description_text{
      flex: 1;
}

.relation_description_text p{
      white-space: pre-line;
      font-size: 1.4rem;
}

.inner_relation > a{
      width: 7rem;
      height: 2.8rem;
      background: var(--bg-100);
      border-radius: .4rem;
      border: solid .2rem var(--theme-200);
      box-shadow: .5rem .5rem var(--theme-200);
      text-transform: uppercase;
      font-size: 1.2rem;
      color: var(--theme-300);
      margin-top: 3rem;
      margin-left: calc(100% - 8rem);
}