.pager_aside{
      flex-direction: column;
      width: 100%;
      height: 100%;
      align-items: center;
      padding: 2.5rem 1.5rem;
}

.pager_current{
      width: 9rem;
      height: auto;
      margin-top: 4rem;
      flex-direction: column;
      align-items: center;
}

.pager_current__icon{
      width: 9rem;
      height: 9rem;
      border-radius: 50%;
      background: rgba(0,0,0,.3);
      color: var(--theme-100);
}

.pager_current__icon *{
      font-size: 4rem;
}

.pager_current__title{
      width: 8rem;
      height: 4rem;
      position: relative;
      justify-content: center;
      margin-top: 3rem;
}

.pager_current__title:before{
      width: 100%;
      height: .2rem;
      background: var(--bg-100);
      left: 0;
      bottom: 0;
}

.pager_current__title:after{
      width: 3.6rem;
      height: 1.2rem;
      background: var(--theme-200);
      bottom: -.5rem;
}

.pager_current__title span{
      color: var(--text-100);
      text-transform: uppercase;
      font-family: var(--ft-t-1);
}