.inner_photos{
      width: 100%;
      height: 100%;
      padding: 1rem;
      position: relative;
      z-index: 3;
}

.photos_content{
      width: 100%;
      height: 100%;
      position: relative;
}

.photos_content:before{
      width: 21.5rem;
      height: 34.5rem;
      background: var(--bg-400);
      left: 0;
      top: 0;
}

.photos_content:after{
      width: 21rem;
      height: 16rem;
      background: var(--theme-200);
      right: 0;
      bottom: 0;
      z-index: -1;
}

.photo_card__gallery{
      width: 100%;
      height: 100%;
      padding: 2rem;
      position: relative;
      z-index: 10;
}

.photo_card__gallery_back p{
      width: 24rem;
      height: 10rem;
      position: absolute;
      right: 0;
      top: 4rem;
      font-size: .9rem;
      text-align: justify;
      opacity: .3;
}

.photo_card__gallery_back h1{
      font-size: 13rem;
      position: absolute;
      right: 0rem;
      top: 5rem;
      font-weight: 800;
      font-family: var(--ft-t-3);
      width: fit-content;
      height: fit-content;
}

.photo_card__gallery_img{
      width: 31rem;
      height: 42rem;
      position: relative;
      box-shadow: 1rem 1rem 20px rgba(0,0,0,.2);
}

button.slick-arrow{
      width: 5.6rem;
      height: 5.6rem;
      background: var(--theme-200);
      border-radius: 50%;
      border: solid .4rem var(--theme-300);
      z-index: 2;
      top: 23rem;
}

button.slick-arrow:hover{
      background: var(--theme-200);
}

button.slick-arrow.slick-next{
      right: 10.5rem;
}

button.slick-arrow.slick-prev{
      left: -3.2rem;
      visibility: hidden;
}

button.slick-arrow.slick-next:before,
button.slick-arrow.slick-prev:before{
      width: 1.4rem;
      height: 1.4rem;
      color: transparent;
      transform: rotate(45deg);
      top: 1.45rem;
      left: .8rem;
}

button.slick-arrow.slick-next:before{
      border-top: solid .5rem var(--theme-300);
      border-right: solid .5rem var(--theme-300);
      left: 1rem;
}


button.slick-arrow.slick-prev:before{
      border-bottom: solid .1rem var(--text-100);
      border-left: solid .1rem var(--text-100);
      left: 1.4rem;
}

.slick-prev, .slick-next {
      background: var(--theme-200) !important;
  }